.messageTextCenter {
  margin: 5px;
  padding: 1px 5px;
  border-radius: 2px;
  border: 1px solid grey;
}

.messageContainer {
  display: flex;
  padding: 7px;
  border-radius: 3px;
  margin: 0 10px;
  align-items: center;
  text-align: left;
  background: rgba(55, 0, 255, 0.15);
  box-shadow: -1px -1px 6px 0 #0185c2;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 10px;
  border-radius: 4px;
}

.containerLeft {
  box-sizing: border-box;
  clear: both;
  display: flex;
  align-items: center;
  margin: 5px 15% 5px 10px;
  float: left;
}

.containerRight {
  box-sizing: border-box;
  clear: both;
  display: flex;
  align-items: center;
  margin: 5px 10px 5px 15%;
  float: right;
  flex-direction: row-reverse;
}

.containerCenter {
  clear: both;
  display: flex;
  justify-content: center;
}

.image {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.messageText {
  margin: 0 30px 0 10px;
  color: white;
  font-size: 12px;
  font-family: monospace;
}

.messageTime {
  font-size: 7px;
  padding-top: 8px;
  color: #36c7e9;
}

@media screen and (min-width: 1000px) {
  .messageText {
    font-size: 15px;
  }
}
@media screen and (min-width: 1400px) {
  .messageText {
    font-size: 15px;
  }
}

@media screen and (max-width: 900px) {
  .messageText {
    font-size: 13px;
  }
  .messageContainer {
    max-width: 90%;
  }
}
