.messageTextCenter {
  margin: 5px;
  padding: 1px 5px;
  border-radius: 2px;
  border: 1px solid grey;
}

.messageContainer {
  /* display: flex; */
  padding: 7px;
  border-radius: 3px;
  margin: 0 10px;
  text-align: left;
  /* background: rgba(55, 0, 255, 0.15); */
  /* box-shadow: -1px -1px 6px 0 #0185c2; */
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 10px;
  border-radius: 4px;
  display: flex;
  align-items: flex-start;
  margin-bottom: 10px;
  width: 100%;
}

.containerLeft {
  /* box-sizing: border-box; */
  /* clear: both; */
  /* display: flex; */
  /* align-items: center; */
  align-items: flex-start;
  /* margin: 5px 15% 5px 10px; */
  /* float: left; */
  display: flex;
  margin-bottom: 5px;
  width: 100%;
}

.messageBy {
  margin: 0 30px 0 10px;
  color: orangered;
  font-weight: 500;
  font-size: 12px;
  font-family: monospace;
  background: -webkit-linear-gradient(rgb(117, 4, 192), rgb(224, 4, 158));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  /* float: right; */
  /* padding-right: 5px; */

  /* display: flex; */
}

.sendIcon {
  /* flex: 7; */
  font-size: 15px !important;
  /* margin: 10px 5px; */
  fill: rgb(224, 4, 158) !important;
  width: 1em;
  height: 1em;
  font-size: 1.5rem;
  padding-top: 5px;
  padding-right: 20px;
  /* padding: 4px !important; */
  transition: 0.7s ease-in-out;
  /* float: right; */

  /* transition: fill 200ms cubic-bezier(0.4, 0, 0, 0.82) 0ms; */
}

.messageText {
  margin: 0 30px 0 10px;
  color: white;
  font-size: 12px;
  font-family: monospace;
  /* float: right; */
  /* display: flex; */
}

@media screen and (min-width: 1000px) {
  .messageText {
    font-size: 15px;
  }
}
@media screen and (min-width: 1400px) {
  .messageText {
    font-size: 15px;
  }
}

@media screen and (max-width: 900px) {
  .messageText {
    font-size: 13px;
  }
  .messageContainer {
    max-width: 90%;
  }
}
