@import url("https://fonts.googleapis.com/css2?family=Patrick+Hand+SC&display=swap");

.cardBody {
  backdrop-filter: blur(6px) saturate(185%);
  -webkit-backdrop-filter: blur(6px) saturate(185%);
  /* background-color: rgba(5, 33, 59, 0.03); */
  background: rgb(0 0 0 / 38%);
  border-radius: 12px;
  /* border: 1px solid rgba(209, 213, 219, 0.3); */
  /* margin-left: 10%; */
  margin-left: auto;
  margin-right: auto;
  padding-top: 0px;
  width: 560px;
  box-shadow: rgb(199 24 147) 0px 0px 2px, rgb(224 4 158) -2px 4px 8px,
    rgb(117 4 192) 1px -6px 8px;

  /* background: -webkit-linear-gradient(rgb(117, 4, 192), rgb(224, 4, 158));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; */
  /* height: 80vh; */
}
.cardBodywithcomments {
  backdrop-filter: blur(6px) saturate(185%);
  -webkit-backdrop-filter: blur(6px) saturate(185%);
  /* background-color: rgba(5, 33, 59, 0.03); */
  background: rgb(0 0 0 / 38%);
  border-radius: 12px;
  /* border: 1px solid rgba(209, 213, 219, 0.3); */
  /* margin-left: 10%; */
  margin-left: auto;
  margin-right: auto;
  padding-top: 0px;
  width: 560px;
  box-shadow: rgb(199 24 147) 0px 0px 2px, rgb(224 4 158) -2px 4px 8px,
    rgb(117 4 192) 1px -6px 8px;
  margin-top: 70px;
  /* height: 80vh; */
}
.cardBody {
  margin-top: 70px;
}

.userimg {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  /* padding: 5px; */
  margin-left: 20px;
  margin-top: 10px;
  float: left;
}

.content {
  width: 100%;
}

.name {
  color: hsl(210, 75%, 54%);
  font-size: 20px;
  font-weight: lighter;
  margin: 10px;
  float: left;
  text-transform: capitalize;
  font-family: "Patrick Hand SC", cursive;
  font-weight: 600;
  margin-top: 10px;
  background: -webkit-linear-gradient(rgb(117, 4, 192), rgb(224, 4, 158));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.cardImage {
  border-radius: 20px;
  width: 520px;
  height: 520px;
  object-fit: cover;
  /* height: 90%; */
}
.cardContent {
  display: flex;
  justify-content: space-between;
  padding-left: 15px !important;
}
.cardContent button {
  padding-top: 0px !important;
}
.likeBtn {
  padding-top: 0px !important;
  fill: rgba(101, 104, 143, 0.712) !important;
  /* fill: hsl(210, 75%, 54%) !important; */
  font-size: 23px !important;
}
.likeBtn:hover {
  transform: rotate(360deg) scale(1.3);
  transition: 1s;
}
.active {
  padding-top: 0px !important;
  font-size: 23px !important;
  fill: rgb(224, 4, 158) !important;
}
.likes {
  color: hsl(210, 75%, 54%);
  font-size: 15px;
  margin: 0px 6px;
  font-family: "Patrick Hand SC", cursive;
  font-weight: 600;
}

.comments {
  color: white;
  display: flex;
  align-items: center;
}
.comments:hover {
  cursor: pointer;
}
.arrow:hover {
  transform: rotate(180deg);
}
.input {
  padding: 5px;
  border: none;
  /* border-top: solid 1px orangered; */

  border-bottom: solid 1px rgb(224, 4, 158);
  background-color: transparent;
  border-radius: 10px;
  outline: none;
  margin-left: auto;
  margin-right: auto;
  width: 85%;
  box-shadow: rgb(199 24 147) 0px 0px 2px;
  background: -webkit-linear-gradient(rgb(117, 4, 192), rgb(224, 4, 158));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.input::placeholder {
  text-align: center;
  color: white;
  background: -webkit-linear-gradient(rgb(117, 4, 192), rgb(224, 4, 158));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.viewcomments {
  color: rgb(255, 255, 255);
  font-size: 15px;
  margin-top: 8px;
  font-family: pacifico;
  background: -webkit-linear-gradient(rgb(117, 4, 192), rgb(224, 4, 158));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.commentsdivhidden {
  margin-top: 5px;
  backdrop-filter: blur(6px) saturate(185%);
  -webkit-backdrop-filter: blur(6px) saturate(185%);
  /* background-color: rgba(5, 33, 59, 0.03); */
  background: #000a1c;
  border-radius: 12px;
  /* border: 1px solid rgba(209, 213, 219, 0.3); */
  /* margin-left: 10%; */
  margin-left: auto;
  margin-right: auto;
  padding-top: 0px;
  width: 560px;
  box-shadow: rgb(117 4 192) 0px 4px 6px;
  max-height: 200px;
  overflow-y: scroll;
}

.commentstitle {
  color: white;
  font-size: 15px;
  font-weight: lighter;
  margin: 10px;
  text-transform: capitalize;
  font-family: monospace;
  font-weight: 600;
  margin-top: 10px;
  background: -webkit-linear-gradient(rgb(117, 4, 192), rgb(224, 4, 158));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.sendIcon {
  /* flex: 7; */
  font-size: 15px !important;
  /* margin: 10px 5px; */
  fill: rgb(224 4 158) !important;
  width: 1em;
  height: 1em;
  display: inline-block;
  font-size: 1.5rem;
  padding: 4px !important;
  transition: 0.7s ease-in-out;

  /* transition: fill 200ms cubic-bezier(0.4, 0, 0, 0.82) 0ms; */
}
.delIcon {
  /* flex: 7; */
  font-size: 25px !important;
  /* margin: 10px 5px; */
  fill: #03a2ad !important;
  width: 1em;
  height: 1em;
  font-size: 1.5rem;
  padding-top: 10px;
  padding-right: 20px;
  /* padding: 4px !important; */
  transition: 0.7s ease-in-out;
  /* float: right; */

  /* transition: fill 200ms cubic-bezier(0.4, 0, 0, 0.82) 0ms; */
}

