    font-size: 25px;
    margin: 10px;
    margin-top: 10px;
  }
  .cardImage {
    width: 620px;
    height: 620px;
  }
  .likeBtn {
    padding-top: 0px !important;
    font-size: 33px !important;
  }
  .active {
    padding-top: 0px !important;
